.intro_container {
    display: flex;
    flex-direction: row;
}

.intro {
    width: 50%;
    align-items: center;
    justify-content: center;
    font-size: 1.2em; 
    font-family: 'IBM Plex Sans', sans-serif;
    line-height: 200%;
}

.me {
    width: 15em;
    height: 16em;
    position: relative;
    left: 20%;
    top: 20%;
    border-radius: 100%;
}

.top_left_stars {
    display: flex;
    /* background: blueviolet; */
    width: 100vw;
    height: 8em;
}

.star_decor0 {
  height: 2em;
  position: relative;
  left: 88%;
  font-size: 3em;
  font-family: 'Corben', cursive;
  color: var(--green);
}

.star_decor {
  background: yellowgreen;
  position: relative;
  left: 90%;
  bottom: 1em;
  font-size: 8em;
  font-family: 'Elsie', cursive;
  color: var(--green);
  /* margin: 0.3em; */
}

.star_decor1 {
  width: 1em;
  position: absolute;
  right: 0px;
  background-color: palevioletred;
  font-size: 20em;
  font-family: 'Elsie', cursive;
  color: var(--yellow);
  margin: 0.3em;
}