.Projects{
    /* background-color: lightgreen; */
    /* width: 100vw; */
    min-height: 70em;
    position: relative;
    display: block;
    margin-top: 8em;
    font-family: 'IBM Plex Sans', sans-serif;
}

.projects_title {
    width: 800px;
    height: 80px;
    position: relative;
    font-size: 4rem; 
    letter-spacing: 2.5px;
    font-family: 'Asap', 'Source Sans Pro';
    color: var(--green);
    padding: 0 0 0 150px;
}

.p5 {
    position: absolute;
    top: 400px;
    left: 230px;
}

.byow {
    position: absolute;
    top: 270px;
    left: 850px;
}

.tgif {
    height: 7em;
    position: absolute;
    top: 650px;
    left: 680px;
}

.proj_star {
    width: 1em;
    height: 1em;
    position: absolute;
    bottom: 15px;
    right: 15px;
    text-align: center;
    /* vertical-align: 50%; */
    letter-spacing: 2.5px;
    font-size: 3em;
    font-family: 'Cantata One';
    color: var(--yellow);
    border: 3px; 
    border-style:solid; 
    border-color:var(--red); 
    padding: 0px;
    margin: 0px;
}

.walmart_star {
    /* background-color: aquamarine; */
    width: 1em;
    /* height: 1em; */
    position: absolute;
    top: 0em;
    left: 10em;
    font-size: 6em;
    color: var(--green);
    font-family: 'Balthazar';
    padding: 5px 0px;
}

.projects_bottomleft_star1 {
    /* background-color: aquamarine; */
    width: 1em;
    position: absolute;
    top: 10.5em;
    left: 12em;
    /* width: 1em; */
    line-height: 0;
    font-size: 6em;
    color: var(--green);
    font-family: 'Corben', cursive;
}

.projects_bottomleft_star2 {
    /* background-color: aquamarine; */
    position: absolute;
    top: 15em;
    left: 16em;
    /* width: 1em; */
    line-height: 0;
    font-size: 4em;
    color: var(--green);
    font-family: 'Cantata One', cursive;
    margin: 0.1em;
}
