.Experience{
    /* background-color: blue; */
    width: 100vw;
    width: 90vw;
}

#roles tr td:nth-child(1) {
  font-size: 5em;
  padding: 0 0.1em 0.3em 0.1em;
  /* background: purple; */
}

#roles tr td:nth-child(2) {
  padding: 0 0 0em 0.4em;
  /* background: purple; */
}

table {
  position: relative;
  left: 12%;
  table-layout: fixed;
  width: 40em;
  /* height: 100px; */
  /* background: paleturquoise; */
}

th {
  height: 2em;
  font-size: 4rem;
  font-weight: 200;
  letter-spacing: 2.5px;
  font-family: 'Asap', 'Source Sans Pro';
  text-align: left;
  color: var(--blue);
  /* background: purple; */
  padding: 0;
  margin: 0;
}

tr.border_bottom td, th {
  border-bottom: 2px solid var(--red);
  line-height: 0;
  /* padding: 0 0 0.3em 0; */
  /* background: red; */
}

.date_row {
  line-height: 3em;
  font-size: 0.8em;
  color: var(--yellow);
}

.bullet_cell {
  width: 0%;
  color: var(--yellow);
  font-family: 'Cantata One', serif;
  padding: 0 0.1em 0.3em 0.1em;
}

.position_cell {
  width: 100%;
  color: var(--blue);
  font-size: 1.2em;
  font-family: 'IBM Plex Sans', sans-serif;
  font-weight: 300;
}

.bullet_img {
  width: 0.2em;
  height: 0.2em;
}

.b1 .star {
  width: 10px;
  background-color: palevioletred;
  position: absolute;
  margin: 1px;
  left: 2em;
  color: var(--yellow);
  font-size: 5em;
}

.b2 .star {
  width: 10px;
  background-color: aqua;
  /* color: black; */
  position: absolute;
  margin: 1px;
  left: 2em;
  top: 5em;
  color: var(--yellow);
  font-size: 5em;
}

.bullets {
  background-color: aquamarine;
  font-size: 5em;
  position: absolute;
  left: 2em;
  color: var(--yellow);
  top: 1.25em;

}