:root {
  --yellow: #D39B3E;
  --red: #AF3727;
  --green: #6B9334;
  --blue: #8AC0BC;
  --bg: #FFF8E5;
}

/* * {
  outline: 1px solid #f00 !important;
} */

@font-face {
  font-family: Asap;
  src: url("./assets/fonts/Asap-Medium.otf");
}

@font-face {
  font-family: OpenSans;
  src: url("./assets/fonts/OpenSans-Regular.ttf");
}

@font-face {
  font-family: CantataOne;
  src: url("./assets/fonts/CantataOne-Regular.ttf");
}

@font-face {
  font-family: dm;
  src: url("./assets/fonts/DMSans-Regular.ttf");
}

.App {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: var(--bg);
  /* overflow-x: hidden; */
}

.chinese-name {
  position: fixed;
  /* background: black; */
  width: 2em;
  /* height: 100%; */
  font-size: 3em; 
  font-family: 'Source Sans Pro';
  color: var(--yellow);
  margin: 1vw 1vw 1vw 1vw;
}

.bold {
  font-weight: 800;
  margin-right: 20px;
}

.dragon {
  width: 18vw; 
  position: absolute;
  right: 5em;
  top: 10em;
  border-radius: 100%;
}

.segway {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  letter-spacing: 4px;
  font-size: 1.2em;
  color: var(--red);
  padding: 10vh 0 20vh 0;
  margin: 0;
}

.segway_star {
  background: purple;
  font-size: 3rem;
  line-height: 0;
  font-family: 'Corben', cursive;
  /* color: var(--blue); */
  color: var(--bg);
  text-shadow: -1px 1px 0 var(--green),
              1px 1px 0 var(--blue),
              1px -1px 0 var(--red),
              -1px -1px 0 var(--yellow);
  margin: 0.3em;
}

.goodbye_segway {
  /* background: red; */
  /* height: 20em; */
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  letter-spacing: 4px;
  /* height: 30vh; */
  /* font-size: 4rem; 
  letter-spacing: 2.5px;
  font-family: 'Asap', 'Source Sans Pro';
  color: var(--green); */
  font-size: 1.2em;
  color: var(--red);
  /* background: red; */
  padding: 100px 0 10em 0;
}

