:root {
  --loved_width: 13em;
  --loved_height: 9em;
  --border-rad: 2px;
}

.AboutMe {
    /* width: 100vw; */
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: flex-end;
    margin-top: 10em;
}

.aboutme_container {
  display: flex;
  flex-direction: column;
  padding: 7em 0 0 0;
  width: 30%;
}

.aboutme_title {
  display: flex;
  font-size: 4rem; 
  letter-spacing: 2.5px;
  /* line-height: 1.3em; */
  font-family: 'Asap', 'Source Sans Pro';
  transform: rotate(90deg);
  color: var(--yellow);
  margin: 0 0 2em 0;
}

.love_container {
  border-radius: 10px;
  /* background: teal; */
}

.star_enjoy {
  font-size: 6rem;
  line-height: 3em;
  font-family: 'Corben', cursive;
  color: var(--blue);
  margin: 0.3em;
}

.love {
  width: var(--loved_width);
  height: var(--loved_height);
  border-right: 15px solid var(--yellow);
  margin: 25px;
  border-radius: var(--border-rad);
}

.caption {
  position: relative;
  bottom: 3em;
  font-size: 1em;
  font-family: 'IBM Plex Sans', sans-serif;
  letter-spacing: 1px;
  color: var(--blue);
  padding: 2em 0 0 2em;
}
